.promo {
  padding-top: 70px;
  padding-bottom: 75px;

  background-color: #f1f1f1;

  &--toys {
    position: relative;

    padding-bottom: 328px;

    background-image: url("../img/hero-mobile@1x.jpg");
    background-position: center -18px;
    background-repeat: no-repeat;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/hero-mobile@2x.jpg");
      background-size: 575px auto;
    }
  }
}

.promo__title {
  text-align: center;
}

.promo__wrapper {
  position: absolute;
  bottom: 0;
  left: 50%;

  transform: translate(-50%);

  width: 100%;
}

.promo__button {
  position: absolute;

  display: block;

  padding: 25px 90px 30px 28px;

  width: 100%;

  font-size: 19px;
  font-weight: 700;
  color: #ffffff;

  box-sizing: border-box;

  &--style--1 {
    left: 0;
    bottom: 103px;
    background-color: @seafoam-blue;

    .promo__icon {
      top: 23px;
      right: 36px;
    }
  }

  &:hover,
  &:focus {
    padding-top: 30px;
    padding-bottom: 35px;

    .promo__icon {
      opacity: 0.7;
    }
  }

  &:active {
    .promo__icon {
      opacity: 0.3;
    }
  }

  &--style--1:hover,
  &--style--1:focus {
    background-color: #59d5bc;
  }


  &--style--1:active {
    color: @button-font-active;
    background-color: #59d5bc;
  }

  &--style--2 {
    left: 0;
    bottom: 0;
    background-color: @aqua-marine;

    .promo__icon {
      top: 20px;
      right: 26px;
    }
  }

  &--style--2:hover,
  &--style--2:focus {
    background-color: @greeny-blue;
  }

  &--style--2:active {
    color: @button-font-active;
    background-color: @greeny-blue;
  }
}

.promo__button .promo__icon {
  position: absolute;
  opacity: 0.5;

  path {
    fill: #149080;
  }
}

@media (min-width: @tablet-width) {
  .promo {
    position: relative;

    padding-top: 96px;
    padding-bottom: 78px;

    &--toys {
      padding-top: 154px;
      padding-bottom: 489px;

      background-color: #ffffff;
      background-image: url("../img/hero-tablet@1x.jpg");
      background-repeat: no-repeat;
      background-position: center;

      .promo__title br {
        display: none;
      }
    }

    &::after {
      content: "";

      position: absolute;
      bottom: -20px;
      left: 0;

      background-image: url("../img/bg-zigzag-fill.svg");
      background-repeat: repeat;
      background-position: 0 0;

      width: 100%;
      height: 34px;
    }
  }

  .promo__wrapper {
    width: 87%;
  }

  .promo__button {
    padding: 28px 90px 29px 36px;
    width: 50%;

    font-size: 20px;
    line-height: 30px;

    &:hover,
    &:focus {
      padding-top: 33px;
      padding-bottom: 34px;
    }

    &--style--1 {
      bottom: 0;

      .promo__icon {
        top: 30px;
        right: 36px;
      }
    }

    &--style--2 {
      right: 0;
      left: auto;

      .promo__icon {
        top: 32px;
        right: 34px;
      }
    }
  }
}

@media (min-width: @desktop-width) {
  .promo {
    padding-top: 93px;
    padding-bottom: 101px;
  }

  .promo--toys {
    padding-top: 154px;
    padding-bottom: 475px;
    background-image: url("../img/hero-desktop@1x.jpg");

    .promo__title {
      padding: 0 216px;
    }
  }

  .promo__wrapper {
    width: 91.3%;
  }
}
